













































import Vue from "vue";
import {userInfo} from '../../interface/login.ts';
export default Vue.extend({
  data() {
    return {
      signUp: {
        style: { opacity: "0", display: "none" },
        data:{ 
          username:'',
          email:'',
          password:""
        },
        repeatPassWord:""
      },
      login:{
        style: { opacity: "0", display: "none" },
        data:{
          username:'',
          email:'',
          password:""
        }
      },
      onLogin: false,
      onSignUp: false
    };
  },
  async created() {
    let user=await this.ajax.get(`/user/getuserinfo`);
    console.log(user);
    this.pl();
  },
  methods: {
    //点击登录-跳出弹窗动作
    cambiar_login() {
      this.signUp.style.display = "none";
      this.login.style.display = "block";
      this.onLogin = true;
      this.onSignUp = false;
      let timer1 = setTimeout(() => {
        this.signUp.style.opacity = "0";
        clearTimeout(timer1);
      }, 400);
      let timer2 = setTimeout(() => {
        this.login.style.opacity = "1";
        clearTimeout(timer2);
      }, 200);
    },
    //点击提交的动作
    cambiar_login_submit() {},
    //点击注册-跳出弹窗动作
    cambiar_sign_up() {
      this.signUp.style.display = "block";
      this.login.style.display = "none";
      this.onLogin = false;
      this.onSignUp = true;
      let timer1 = setTimeout(() => {
        this.signUp.style.opacity = "1";
        clearTimeout(timer1);
      }, 400);
      let timer2 = setTimeout(() => {
        this.login.style.opacity = "0";
        clearTimeout(timer2);
      }, 100);
    },
    //点击注册-跳出弹窗动作
    async cambiar_sign_up_submit() {
      console.log(this.signUp.data.username)
      let params:userInfo={...this.signUp.data};
      this.ajax.post(`/user/addUserInfo`,params);
    },
    ocultar_login_sign_up() {
      this.onLogin = false;
      this.onSignUp = false;
      this.signUp.style.opacity = "0";
      this.login.style.opacity = "0";
      let timer1 = setTimeout(() => {
        this.signUp.style.display = "none";
        this.login.style.display = "none";
        clearTimeout(timer1);
      }, 500);
    }
  }
});



